<template>
    <div class="middle-box text-center loginscreen  animated fadeInDown">
        <div  >
            <div>
                <p  style="font-size: 80px;color: #e6e6e6;font-weight: 600;margin-bottom: 100px;margin-top: 40px">铁旅e行</p>
            </div>
            <h3>欢迎使用 oms</h3>

            <form class="m-t">
                <div class="form-group">
                    <input type="text" class="form-control" placeholder="用户名" required="" v-model="user">
                </div>
                <div class="form-group">
                    <input type="password" class="form-control" placeholder="密码" required="" v-model="pwd">
                </div>
                <button type="button" class="btn btn-primary block full-width m-b" @click="login()">登 录</button>

            </form>
        </div>
    </div>
</template>

<script>
    import ajax from "../model/sys/index"
    export default {
        data() {
            return {
                user:'',
                pwd:''
            }
        },
        methods: {
            login(){
                var data= {
                    account:this.user,
                    password:this.pwd
                }
                ajax.login(data,res=>{
                    if(res){

                       localStorage.setItem("token",res.data.result.token)

                        this.$message({
                            showClose: true,
                            message: '登录成功',
                            type: 'success'
                        });
                        this.$router.push({path:'index'})
                    }
                })

            }
        },
        mounted() {},
        props: {},
        watch: {}
    }
</script>
<style lang="less">

</style>



