const index = () => import('../../components/index')
const menu = () => import('../../components/sys/menu')
const dept = () => import('../../components/sys/dept')

export default [
    {
        path: '/',
        redirect:'/index',
        meta: { perm: '*' }
    },
    {
        path: '/index',
        name: 'index',
        component: index,
        meta: { perm: '*' }
    },
    {
        path: '/menu',
        name: 'menu',
        component: menu,
        meta: { perm: ':sys:menu' }
    }
    ,
    {
        path: '/dept',
        name: 'dept',
        component: dept,
        meta: { perm: ':sys:dept' }
    }
]



