//axios,请求全局拦截器。post全局方法改变为formdata
function transformRequest (data) {
    let ret = ''
    for (let it in data) {
        if((typeof data[it])=='string'){
            ret += encodeURIComponent(it) + '=' + encodeURIComponent(data[it]) + '&'
        }else {
            ret += encodeURIComponent(it) + '=' + encodeURIComponent(JSON.parse(data[it])) + '&'
        }
    }
    return ret
}


//
axios.interceptors.request.use(function (config) {

    config.headers['Content-Type']='application/json;charset=utf-8';

    if(config.url=="user/loginadmin"){

    }else {
        config.url=config.url+"/"+localStorage.getItem("token")
    }
    // config.data=transformRequest(config.data);
    return config;
}, function (err) {
    return Promise.reject(err);
});

//axios,相应全局拦截器，处理数据接口没有权限

axios.interceptors.response.use(function (response) {
    if(response.data.code == "200"){
        return response
        return
    }else if(response.data.code == "401"){
        router.push({path:'login'})
        return
    }else if(response.data.code == "500"){
        Notification.error({
            title: '错误',
            message: response.data.message
        });
        return
    }else {
        return response
    }
}, function (error) {
    console.log(error)
    Notification.error({
        title: '错误',
        message: "数据异常，请注意输入格式或其他"
    });
     return Promise.reject(error)
})

//获取当前接口所在地址
Vue.prototype.getPathName = function () {
    var name1,name2;
    var thisUrl = location.href.split("#")[1]

    var menus = localStorage.getItem("meuns")
    if(menus){
        menus=JSON.parse(menus)
        for(var i=0;i<menus.length;i++){
            for(var j=0;j<menus[i].children.length;j++){
                if(thisUrl==menus[i].children[j].url){
                    name1 = menus[i].name
                    name2 = menus[i].children[j].name
                }
            }
        }
        localStorage.setItem("name",name1+","+name2)
    }

}




