import Vue from 'vue'
import Router from 'vue-router'

Vue.use(Router)

//菜单的路由数组
import sys from './sys'
import gz_admin from './gz_admin/index.js'
import coin_sys from './coin_sys/index.js'
import main from '../components/main'
import login from '../components/login'
import error from '../components/sys/error'
import noperm from '../components/sys/noperm'
//00

let routerList=[
    {
        path: '/',
        name: 'main',
        component: main,
        children:sys.concat(coin_sys).concat(gz_admin),
        meta: { perm: '*' }
    },
    {
        path:'/login',
        name: 'login',
        component: login,
        meta: { perm: '*' }
    },
    {
        path:'/noperm',
        name: 'noperm',
        component: noperm,
        meta: { perm: '*' }
    },
    {
        path:'*',
        name: 'error',
        component: error,
        meta: { perm: '*' }
    },
]

const router = new Router({
    routes:routerList
})

router.beforeEach((to, from, next) => {
    if(window.permission.indexOf(to.meta.perm?to.meta.perm:'')<0){
        if(from.path!='/login'){
            next({path:'/noperm'})
        }else{
            next({path:'/index'})
        }
    }
    next()
})

router.afterEach((to, from) => {

    //main tab切换页面保存
    if(to.matched[0].name==='main'){
        $('.J_menuTab').each(function () {
            if($(this).hasClass('active')){
                $(this).attr('data-id',to.path.substring(1))
            }
        })
    }
})


export default router



