<template>
    <el-tree class="el-tree" :data="data" :props="defaultProps" @node-click="handleNodeClick"></el-tree>
</template>

<script>
    import ajax from "../../model/sys/index"
    var modules=[];
    export default {
        data() {
            return {
                data: [],
                defaultProps: {
                    children: 'children',
                    label: 'moduleName'
                }
            };
        },
        methods: {
            handleNodeClick(data) {
                var thisUrl = location.href.split("#")[1]

                if(data.url){
                    if(thisUrl == data.url){
                        location.reload()
                    }else {
                        this.$router.push({path:data.url})
                    }

                }else {

                }


            }
        },
        mounted(){

            ajax.adminuserinfo({token:localStorage.getItem("token")},res=>{

                this.data=[{
                    name:"order",
                    moduleName:"管理配置",
                    children:res.data.result.modules
                }]
            })




        }
    };
</script>
<style lang="less">
.el-tree{
    background:#2f4050;
}
.el-tree-node__content{
    height: 36px;
    color: #8095a8;
}

</style>



