import Vue from 'vue'
import Vuex from 'vuex'
Vue.use(Vuex)

const store = new Vuex.Store({
    state: {
        count: 0,
        loginresult:""
    },
    mutations: {
        increment(state) {
            state.count++
        },
        changeMenuList(state,list){
            state.menuList=list;
            localStorage.menuList=JSON.stringify(list)
        },
        changeDeptList(state,list){
            state.deptList=list;
            localStorage.deptList=JSON.stringify(list)
        },
        loginresult(state,data){

            state.loginresult = data
            console.log(state.loginresult)
        },

    }
})

export default store



