<template>
    <div id="page-wrapper" class="gray-bg dashbard-1">

        <div class="row content-tabs" style="text-align: center;background: #2f4050">
            <span style="font-size: 26px;position: absolute;top:15%;left:40px;display: inline-block;color: gainsboro" >铁旅E行后台管理系统</span>

        </div>
        <el-button type="info" @click="logout" plain style="position: absolute;top:15px;right: 20px;height: 30px;line-height: 2px">退出</el-button>

        <div class="row J_mainContent" id="content-main" style="padding: 20px">

                <keep-alive>
                    <router-view  v-if="$route.meta.keepAlive"></router-view>
                </keep-alive>
                <router-view  v-if="!$route.meta.keepAlive"></router-view>
        </div>
        <div class="footer" style="position: absolute;bottom: 0;right: 0;width: 100%;background: #2f4050;color: gainsboro">
            <div class="pull-right">&copy; 2018-2028 铁旅e行OMS
            </div>
        </div>
    </div>
</template>

<script>
    import ajax from "../model/sys/index"
    import vTags from './Tags.vue';
    export default {
        data() {
            return {}
        },
        methods: {
            clearAllCookie() {
                var keys = document.cookie.match(/[^ =;]+(?=\=)/g);
                if(keys) {
                    for(var i = keys.length; i--;)
                        document.cookie = keys[i] + '=0;expires=' + new Date(0).toUTCString()
                }
            },


            logout(){
                localStorage.removeItem("token");

                this.$router.push({path:'login'})
            },

        },
        mounted() {


        },
        props: {},
        watch: {},
        components:{
            vTags
        }
    }
</script>
<style lang="less">
    .content-tabs{
        height: 60px
    }
    #content-main{
        height: calc(92%) !important
    }
    #page-wrapper{
        margin-left: 120px !important;
    }
    body.fixed-sidebar .navbar-static-side, body.canvas-menu .navbar-static-side{
        width:auto !important;
    }
    .el-col-23{
        width: 100% !important;
    }
</style>



