
const gz_order_list = () => import('../../views/gz_order_list')
const gz_order_detail = () => import('../../views/gz_order_detail')
const supplier_order_list = () => import('../../views/supplier_order_list')
const purchase_notice = () => import('../../views/purchase_notice')
const tour_time_cost = () => import('../../views/tour_time_cost')
const ferry_way = () => import('../../views/ferry_way')
const ferry_ticket_inspect = () => import('../../views/ferry_ticket_inspect')
const supplier_config = () => import('../../views/supplier_config')
const account_manage = () => import('../../views/account_manage')
const public_config = () => import('../../views/public_config')
const ticket_inspect_auth = () => import('../../views/ticket_inspect_auth')
const finance_state = () => import('../../views/finance_state')
const index_notice = () => import('../../views/index_notice')

export default [
    {
        path: '/',
        redirect:'/index',
        meta: { perm: '*' }
    },
    {
        path: '/gz_order_list',
        name: 'gz_order_list',
        component: gz_order_list,
        meta: { perm: '*' }
    },
    {
        path: '/gz_order_detail',
        name: 'gz_order_detail',
        component: gz_order_detail,
        meta: { perm: '*' }
    }
    ,
    {
        path: '/supplier_order_list',
        name: 'supplier_order_list',
        component: supplier_order_list,
        meta: { perm: '*' }
    }
    ,
    {
        path: '/purchase_notice',
        name: 'purchase_notice',
        component: purchase_notice,
        meta: { perm: '*' }
    }
    ,
    {
        path: '/tour_time_cost',
        name: 'tour_time_cost',
        component: tour_time_cost,
        meta: { perm: '*' }
    }
    ,
    {
        path: '/ferry_way',
        name: 'ferry_way',
        component: ferry_way,
        meta: { perm: '*' }
    },
    {
        path: '/ferry_ticket_inspect',
        name: 'ferry_ticket_inspect',
        component: ferry_ticket_inspect,
        meta: { perm: '*' }
    },
    {
        path: '/supplier_config',
        name: 'supplier_config',
        component: supplier_config,
        meta: { perm: '*' }
    },
    {
        path: '/account_manage',
        name: 'account_manage',
        component: account_manage,
        meta: { perm: '*' }
    },
    {
        path: '/public_config',
        name: 'public_config',
        component: public_config,
        meta: { perm: '*' }
    },
    {
        path: '/ticket_inspect_auth',
        name: 'ticket_inspect_auth',
        component: ticket_inspect_auth,
        meta: { perm: '*' }
    },
    {
        path: '/finance_state',
        name: 'finance_state',
        component: finance_state,
        meta: { perm: '*' }
    },
    {
        path: '/index_notice',
        name: 'index_notice',
        component: index_notice,
        meta: { perm: '*' }
    }



]



